const initialState = ''
let result = 0;
// check if there is sign in the display
let includingResult = false
export const changeNumberReducer = (state = initialState, action) => {
    if (action.type === 'CHANGE_NUMBER') {
        
        if(action.payload.number===0){
            if(state.length===0){
                return ''
            }else{
                return state+0
            }
        }
        // calculating result
        if (action.payload.number === '=') {
            // added = into the display
            includingResult = true
            result = eval(state)
            return state + '=' + result
        }
        // updating the display
        if (action.payload.number ) {
            if (includingResult === true) {
                // RE for =123
                let re = /=\d+/
                includingResult = false
                if (state.match(re) === null) {
                    return action.payload.number.toString()
                } else {
                    if(isNaN(action.payload.number)===false){
                        
                        return action.payload.number
                    }else{
                        // remove = from the display
                      return  state.match(re).toString().substr(1) + action.payload.number.toString()
                    }
                }
            } else {
                return state + action.payload.number.toString()
            }
        } else {
            return ''
        }
    }
    else {
        return state
    }
}


let includingSign = false;

export const resultReducer = (state = initialState, action) => {
    if (action.type === 'CHANGE_NUMBER') {
        if (action.payload.number==='') {
            return ''
        }
        if(action.payload.number===0){
            if(state.length===0){
                return ''
            }else{
                return state+0
            }
        }
        if (isNaN(action.payload.number) === false) {

            if (includingSign === false) {
                return state + action.payload.number
            } else if (includingSign === true) {
                includingSign = false;
                state = ''
                return state + action.payload.number
            }
        }
        if (action.payload.number === '=') {
            includingSign = true
            return result
        }

        if (action.payload.number === '+' || '-' || '*' || '/') {
            includingSign = true
            return action.payload.number
        }

    } else {
        return state
    }
}

